import React from 'react'
import styled from 'styled-components'
import { useDispatch } from 'react-redux'
// import { changeKPIYear1 } from '../../actions/business_case';

const BCItemsCostSavingNetProdTotalTableTotalDisplayStyled = styled.input`


  border: 0px solid #707070;
  width:100%;
  padding: .5rem;
  font-weight: 500;
  font-family: inherit;
  color: #000000;
  box-sizing: border-box;
  text-overflow: ellipsis;  

  &:active {
    border: none;
  }

  &:focus {
    outline: none;
  }  

  resize: none;
}
`
const BCItemsCostSavingNetProdTotalTableTotalDisplay = () => {
  const dispatch = useDispatch();

  return <BCItemsCostSavingNetProdTotalTableTotalDisplayStyled
            type="number"
            min="0"
            // key={kpi.id}  
            // style={{
            //         backgroundColor: kpi.id % 2 != 0 ? '#b3c7c9' : '#E0E0E0'
            //       }}

            value={0}
            // onChange={(e) => dispatch(changeKPIYear1({id: kpi.id, year1:parseInt(e.target.value ? e.target.value: 0)})) }
          />

}

export default BCItemsCostSavingNetProdTotalTableTotalDisplay