import React from 'react'
import styled from 'styled-components'
import { useDispatch } from 'react-redux'
import { changeSavingItemSavingType } from '../../actions/business_case_items';

const BCItemsSavingPerYearItemTypeStyled = styled.input`


  border: 0px solid #707070;
  width:100%;
  padding: .5rem;
  font-weight: 500;
  font-family: inherit;
  color: #000000;
  box-sizing: border-box;
  text-overflow: ellipsis;  

  &:active {
    border: none;
  }

  &:focus {
    outline: none;
  }  

  resize: none;
}
`
const BCItemsSavingPerYearItemType = ({savingItem}) => {
  const dispatch = useDispatch();

  return <BCItemsSavingPerYearItemTypeStyled
            type="text"
            key={savingItem.id}
            style={{
                    backgroundColor: savingItem.id % 2 != 0 ? '#b3c7c9' : '#E0E0E0'
                  }}

            value={savingItem.saving_type}
            onChange={(e) => dispatch(changeSavingItemSavingType({id: savingItem.id, saving_type:e.target.value})) }
            />

}

export default BCItemsSavingPerYearItemType

