import React from 'react'
import styled from 'styled-components'
import { useDispatch } from 'react-redux'
import { changeImpact } from '../../actions/risk_register';

const RRRiskItemImpactStyled = styled.input`


  border: 0px solid #707070;
  width:100%;
  padding: .5rem;
  font-weight: 500;
  font-family: inherit;
  color: #000000;
  box-sizing: border-box;
  text-overflow: ellipsis;  

  &:active {
    border: none;
  }

  &:focus {
    outline: none;
  }  

  resize: none;
}
`
const RRRiskItemImpact = ({risk}) => {
  const dispatch = useDispatch();

  return <RRRiskItemImpactStyled
            type="text"
            key={risk.id}
            style={{
                    backgroundColor: risk.id % 2 != 0 ? '#b3c7c9' : '#E0E0E0'
                  }}

            value={risk.impact}
            onChange={(e) => dispatch(changeImpact({id: risk.id, impact:e.target.value})) }
            />

}

export default RRRiskItemImpact

